.list-items {
    display: flex;
    margin: auto;
    max-width: 800px;
}

.list-items ul {
    width: 100%;
}

.list-items ul li {
    display: flex;
    padding: 5px 5px;
}

.list-items ul li h3 {
    margin-left: auto;
    font-family: var(--font-family);
}

.list-items ul li i {
    color: #991717
}