.Apps h1 {
    text-align: center;
    font-size: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-family: var(--font-family);
    color: #5f0101
}

.grid-container:after {
    content: "";
    display: table;
    clear: both;
  }

.column {
  flex-direction: column;
  padding-left:2.5%;
  padding-right:2.5%;
  float: left;
  width: 50%;
}

@media screen and (max-width: 600px) {
    .column {
      width: 100%;
    }
  }