.bonisitalian__header {
    display: flex;
    margin: 0;
    padding: 0;
}

.bonisitalian__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.bonisitalian__header-content h1 {
    font-family: var(--font-family);
    color: #000;
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.bonisitalian__header-content p {
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    
    margin-top: 2rem
}

.bonisitalian__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
}

.bonisitalian__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bonisitalian__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .bonisitalian__header {
        flex-direction: column;
    }

    .bonisitalian__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .bonisitalian__header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .bonisitalian__header p {
        font-size: 16px;
        line-height: 24px;
    }

    .bonisitalian__header-content__input input,
    .bonisitalian__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 450px) {
    .bonisitalian__header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .bonisitalian__header p {
        font-size: 14px;
        line-height: 24px;
    }

    .bonisitalian__header-content__input input,
    .bonisitalian__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}