.bonisitalian_about {
    background: radial-gradient(circle at 5% 5%, rgba(167, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 40%);
    padding:0;
    margin:0;
}

.aboutUs {
    display: flex;
    flex-direction: column;
    padding-bottom: 2rem;
    margin-left: 3rem;
    margin-right: 3rem;
}

.aboutUs h1 {
    text-align: center;
    font-size: 100px;
    font-family: var(--font-family);
}

.aboutUs p {
    text-align: center;
    font-size: 20px;
    font-family: var(--font-family);
}

.heading-secondary {
    text-align: center;
    padding-bottom: 10px;
    font-size: 100px;
}

.profilesTop{
    display: flex;
    flex-direction: row;
    padding-bottom: 2rem;
}

.profilesBottom{
    display: flex;
    flex-direction: row;
}