.profile {
    display: flex;
    margin: auto;
    border: #000;
    border-width: 2px;
    max-width: 250px;
}

.profile img {
    float: left;
    width:  250px;
    height: 400px;
    object-fit: cover;
    border-radius: 6%;
}

.profile ul li {
    display: flex;
    padding: 5px 5px;
}

.profile ul li h3 {
    margin-left: auto;
    text-align: center;
    font-size: 20px;
    font-family: var(--font-family);
}

.profile ul li p {
    color: #810101
}