.bonisitalian__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem
}

.bonisitalian__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bonisitalian__navbar-links_logo {
    margin-right: 2rem;
}

.bonisitalian__navbar-links_logo img{
    width: 134px;
    height: 131px;
}

.bonisitalian__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.bonisitalian__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.bonisitalian__navbar-links_container p,
.bonisitalian__navbar-sign p,
.bonisitalian__navbar-menu_container p {
    color:#000;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 25px;
    line-height: 32px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.bonisitalian__navbar-sign button,
.bonisitalian__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #270101;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.bonisitalian__navbar-menu {
    color: #270101;
    margin-left: 1rem;
    display: none;
    position: relative;
}

.bonisitalian__navbar-menu svg {
    cursor: pointer;
}

.bonisitalian__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2)
}

.bonisitalian__navbar-menu_container p {
    margin: 1rem 0;
}

.bonisitalian__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .bonisitalian__navbar-links_container{
        display: none;
    }

    .bonisitalian__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .bonisitalian__navbar{
        padding: 2rem 4rem
    }
}

@media screen and (max-width: 550px) {
    .bonisitalian__navbar{
        padding: 2rem;
    }

    .bonisitalian__navbar-sign {
        display: none;
    }

    .bonisitalian__navbar-menu_container {
        top: 20px;
    }

    .bonisitalian__navbar-menu_container-links-sign {
        display: block ;
    }
}