.bonisitalian_contact {
    background: radial-gradient(circle at 5% 5%, rgba(167, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 40%);
    padding:0;
    margin:0;
}

.contactUs {
    display: flex;
    align-items: center;
    padding-left: 3rem;
    padding-right: 3rem;
}

.contactUs_header{
    text-align: center;
    font-size: 60px;
    font-family: var(--font-family);
}

.contactUs p {
    font-family: var(--font-family);
    font-size: 30px;
}

.boldText {
    font-weight: bold;
}

.contactUs_image {
    padding-left: 2rem;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contactUs_image img {
    width: 100%;
    height: 100%;
}